.App {
  font-family: 'Roboto Mono', monospace;
}

.header-container {
  padding: 8px;
  font-family: 'Roboto Mono', monospace;
}

.jumbotron-fluid {
  margin: 0 auto;
  padding: 0;
  font-family: 'Roboto Mono', monospace;
}
.table {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  background-color: dimgray;
  font-family: 'Roboto Mono', monospace;
}

.display-4 {
  text-align: center;
  font-size: xx-large;
  border: 2px solid black;
  border-radius: 30px;
  background: white;
}

.employee-image {
  border: solid 2px;
  border-radius: 20px;
}

.employee-name {
  font-weight: bold;
  text-align: center;
}

.header-desc {
  text-align: center;
}

.form-group {
  text-align: center;
  font-family: 'Roboto Mono', monospace;
}

.form-control {
  display: flex;
  justify-content: 'center';
  align-items: 'center';
  width: 500px;
}

.form-container {
  margin: 0 auto;
  align-self: center;
  border: 2px solid black;
  background-color: dimgrey;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.results {
  border: 2px solid black;
  background: white;
}
